html,
body {
    font-family: $font-primary;
    width: 100%;
    height: 100%;
    -webkit-font-smoothing: antialiased;
}

html {
    &.is-locked {
        top: 0;
        left: 0;
        right: 0;
        position: fixed;
    }
}

body {
    font-family: $font-primary;
    background-color: $black;
    color: $white;
    position: relative;
}
